export function generateMockData() {
    let mockData = [];
    const stores = ["Amazon", "Ebay", "Asos", "Google"];
    const pictures = [
        "data:image/png;base64,",
        "http://cdn.shopify.com/s/files/1/0411/0994/6527/files/Cool_Items_Co_1200x1200.png?v=1592351914",
        "https://cdn.shopify.com/s/files/1/0061/0900/3891/t/2/assets/logo.png?v=661797163124097832",
        "https://getcoolitems.com/wp-content/uploads/2019/07/get-cool-items-logo.png",
        ""
    ];

    for (let i = 0; i < 20; i++) {
        const name = Math.random().toString(36).replace(/[^a-z]+/g, '').substr(0, 5);
        const price = Math.random() * 100;
        const store = stores[Math.floor(Math.random() * 4)];
        const deliveryEstimate = new Date((Math.random() * (1653026128401 - 1610026128401)) + 1610026128401);
        const picture = pictures[Math.floor(Math.random() * 5)];
        mockData.push({ name, price, store, picture, deliveryEstimate, id: i + 1 });
    }

    return mockData;
}
